import React from 'react'
import { SidebarContainer,Icon,CloseIcon, SidebarWrapper, SidebarMenu, SidebarLink,SideBtnWrap, SidebarRoute  } from '../Styles/SidebarElements';
import { Outlet } from 'react-router-dom'

const logout = (e) => {
    localStorage.clear();
    window.location.reload(false);
}

export default function StudentsSide({isOpen,toggle}){
  return (
    <>
    <SidebarContainer isOpen={isOpen} onClick={toggle}>
    <Icon onClick={toggle}>
       <CloseIcon />
    </Icon>
    <SidebarWrapper>
       <SidebarMenu>
       <SidebarLink to="/Students/Home" onClick={toggle}> </SidebarLink>
        <SidebarLink to="/Students/ExamReservation" onClick={toggle}> </SidebarLink>
       </SidebarMenu>
       <SideBtnWrap>
       <SidebarRoute  onClick={e => logout(e)}> </SidebarRoute>
       </SideBtnWrap>
    </SidebarWrapper>
   </SidebarContainer>
   <Outlet />
   </>
  )
}
